import React, { Component } from "react";
import Swal from 'sweetalert2';
import { Cookies } from "react-cookie";
import { HOST_DOMAIN, INSTA_CLIENT_ID } from '../../../../../../Services/Api'
import { baseUrl } from "../../../../../../Assets/AssetsHelper";
import ProfileImage from "../../../../CommonComponents/ProfileImage/ProfileImage";
import SocialGoogleLogin from '../../../../SocialLogin/YouTube'
import { GET_NETWORK_ICON, CAPITALIZE_FIRST_LETTER, GET_NETWORK_NAME } from '../../../../../../Services/Utils/utils'
import { flickrConnect, tumblerConnect } from '../../../../SocialLogin/auth'
import { getFbAuthUrl, getYammerAuthUrl, getVimeoAuthUrl, tiktokAuthentication } from '../../../../../../Layouts/Main/AuthCallback/AuthSocialAcc'
import { addFacebookAccount, addInstaAccount } from '../../../../../../Layouts/Main/AuthCallback/connectSocialAcc'
import { addUserRights } from '../../../../../../Services/Actions/addFeedAction'
import { getRightsInstagramDetails, getFacebookDetails } from '../../../../../../Services/Actions/publishActions'
import { getTwitterCode } from '../../../../../../Services/Actions/loginActions'
const channel = new BroadcastChannel('connect');
const cookies = new Cookies();
class GetRightsSocialAccount extends Component {
    state = {
        connected: null,
        showConfirm: false
    }
    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
        this.onLoadSocialAccount()
    }
    onLoadSocialAccount = () => {
        try {
            const { modalAssetData } = this.props
            if (cookies.get(`ugcRights${modalAssetData.networkId}`)) {
                const accountsData = cookies.get(`ugcRights${modalAssetData.networkId}`)
                if (accountsData && accountsData.length > 0) {
                    this.setState({ access_token: "", socialData: null, accountsData }, () => this.props.onUpdateData(String(GET_NETWORK_ICON(modalAssetData.networkId)).toLowerCase(), accountsData))
                }

            }
        }
        catch (ex) {

        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId, type } = response;

        var self = this
        if (type == "get-rights") {
            const responseToken = JSON.parse(event.data);
            const { modalAssetData } = this.props
            if (modalAssetData.networkId == 2 || modalAssetData.networkId == 18) {
                addInstaAccount(data, (data) => {
                    getRightsInstagramDetails({ access_token: data.token }).then((response) => {

                        const { data } = response
                        var accountsData = [];
                        if (data && Object.keys(data).length > 0) {
                            accountsData.push({ id: data.id, name: data.username, picture: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                        }
                        cookies.set(`ugcRights${modalAssetData.networkId}`, JSON.stringify(accountsData), { path: '/' })
                        self.setState({ access_token: data.token, socialData: response.data, accountsData }, () => this.props.onUpdateData("instagram", accountsData))
                    })
                })
            }
            else if (modalAssetData.networkId == 3) {
                addFacebookAccount(data, networkId, (data) => {
                    getFacebookDetails({ access_token: data.token }).then((response) => {
                        const { pages } = response.data
                        var accountsData = [];
                        if (pages && pages.length > 0) {
                            pages.map((item) => {
                                accountsData.push({ id: item.id, name: item.name, access_token: item.access_token, picture: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                            })
                        }
                        cookies.set(`ugcRights${modalAssetData.networkId}`, JSON.stringify(accountsData), { path: '/' })
                        self.setState({ access_token: data.token, socialData: response.data, accountsData }, () => this.props.onUpdateData("facebook", accountsData))
                    })
                })
            }
            else if (modalAssetData.networkId == 1) {
                var accountsData = [];
                if (data && Object.keys(data).length > 0) {
                    accountsData.push({ id: data.id_str, name: data.name, picture: data.picture ? data.picture : baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                }
                cookies.set(`ugcRights${modalAssetData.networkId}`, JSON.stringify(accountsData), { path: '/' })
                self.setState({ access_token: localStorage.getItem("tokenSecret"), socialData: data, accountsData }, () => this.props.onUpdateData("twitter", accountsData))
            }
            else if (modalAssetData.networkId == 28) {
                var accountsData = [];
                if (data && Object.keys(data).length > 0) {
                    accountsData.push({ id: data.display_name, name: data.display_name, picture: data.avatar ? data.avatar : baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                }
                cookies.set(`ugcRights${modalAssetData.networkId}`, JSON.stringify(accountsData), { path: '/' })
                self.setState({ access_token: data.client_key, socialData: data, accountsData }, () => this.props.onUpdateData("tiktok", accountsData))
            }
            else {
                addUserRights(data, networkId, (data) => {

                })
            }

        }
    }


    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    handleYes = event => {
        this.setState({ showConfirm: true })
    }
    handleNo = event => {
        this.setState({ showConfirm: true })
    }
    onClickSocialConnect = (data) => event => {
        if (data && Object.keys(data).length > 0) {
            Swal.fire({
                imageUrl: baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(data.networkId)}.svg`),
                imageHeight: 40,
                imageWidth: 40,
                title: `<strong>${CAPITALIZE_FIRST_LETTER(GET_NETWORK_ICON(data.networkId))}</strong>`,
                html: `Add an account to continue`,
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: true,
                confirmButtonText: `Continue with ${CAPITALIZE_FIRST_LETTER(GET_NETWORK_ICON(data.networkId))}`,
                confirmButtonAriaLabel: `Continue with ${CAPITALIZE_FIRST_LETTER(GET_NETWORK_ICON(data.networkId))}`,
            }).then((result) => {
                if (result.isConfirmed) {
                    localStorage.setItem("networkId", data.networkId);
                    localStorage.setItem("connectionType", "get-rights");
                    switch (data.networkId) {
                        case 1:
                            getTwitterCode();
                            break;
                        case 6:
                            flickrConnect(6);
                            break;
                        case 21:
                            getYammerAuthUrl()
                            break;
                        case 11:
                            tumblerConnect(11);
                            break;
                        case 8:
                            getVimeoAuthUrl()
                            break;
                        case 3:
                            getFbAuthUrl(3);
                            break;
                        case 28:
                            tiktokAuthentication();
                            break;

                        case 2:
                            const url1 = `https://api.instagram.com/oauth/authorize?client_id=${INSTA_CLIENT_ID}&redirect_uri=${HOST_DOMAIN}/instagram/feeds/authCallback/&scope=user_profile,user_media&response_type=code`
                            window.open(url1, '_blank', "width=786,height=786");
                            break;
                        case 18:
                            const url = `https://api.instagram.com/oauth/authorize?client_id=${INSTA_CLIENT_ID}&redirect_uri=${HOST_DOMAIN}/instagram/feeds/authCallback/&scope=user_profile,user_media&response_type=code`
                            window.open(url, '_blank', "width=786,height=786");
                            break;
                        // case 18:
                        //     getFbAuthUrl(18)
                        //     break;
                        default:
                            break;
                    }
                }
            });
        }
    }


    render() {
        const { showConfirm } = this.state
        const { modalAssetData, connectedAccount } = this.props;
        return <><div className="p-lg-6 p-3">
            <div className="m-auto">
                {
                    connectedAccount && connectedAccount.data.length > 0 ? <><h3 className="mb-2 pb-2 fs-7 pt-1">
                        <span className="fw-medium">Connect Account</span>
                    </h3>
                        <div className="mb-3">
                            <button type="button" className="d-flex align-items-center rounded-1 mb-2 p-3 btn btn-outline-secondary w-100">
                                <div className="d-flex align-items-center flex-grow-1 w-75">
                                    <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                        imageSize={40}
                                        symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                        name={connectedAccount.data[0].name} profileUrl={''} networkId={modalAssetData.networkId} />
                                    <div className="w-75 overflow-hidden">
                                        <span className="text-start fs-7 d-block fw-bolder text-dark text-ellipsis">@{connectedAccount.data[0].name}</span>
                                    </div>
                                </div>
                                <div className="d-flex">
                                    <span className="h-20px w-20px">
                                        <img className="object-center-cover" src={baseUrl(`media/icons/social-icons/${connectedAccount.provider}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(connectedAccount.provider)} />
                                    </span>
                                </div>
                            </button>
                            <span className="fw-medium">You are logged in with this account</span>
                        </div>
                        <div className="d-flex align-items-center w-75 mb-3 mx-auto">
                            <span className="border-bottom d-flex flex-grow-1"></span>
                            <span className="d-flex flex-shrink-0 mx-5 fs-6 fw-semibold text-gray-400">Or</span>
                            <span className="border-bottom d-flex flex-grow-1"></span>
                        </div>
                    </> : null
                }

                <>{
                    modalAssetData.networkId == 7 ?
                        <SocialGoogleLogin onUpdateData={this.props.onUpdateData} />
                        :
                        <button type="button" className="d-flex align-items-center rounded-1 mb-2 p-3 btn btn-outline-secondary w-100" onClick={this.onClickSocialConnect(modalAssetData)}>
                            <div className="d-flex align-items-center flex-grow-1 w-75">
                                <div className="symbol symbol-40 bg-light rounded-circle flex-shrink-0 me-2 overflow-hidden"></div>
                                <div className="w-75 overflow-hidden">
                                    <span className="text-start fs-7 d-block fw-bolder text-ellipsis">Add Account</span>
                                </div>
                            </div>
                            {modalAssetData.networkId ?
                                <div className="d-flex">
                                    <span className="h-20px w-20px">
                                        <img className="object-center-cover" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(modalAssetData.networkId)} style={{ filter: 'grayscale(1)', opacity: '.5' }} />
                                    </span>
                                </div>
                                : null}
                        </button>
                }


                </>

            </div>
        </div>


        </>
    }
}

export default GetRightsSocialAccount;